.--main-phone-bg {
  background-position: -46px 0;
  background-size: 468.32px 634.15px;
  flex-basis: 380.32px;
  height: 581.15px;
  margin-bottom: 12px;
  margin-right: 32px;
  background-image: url(https://www.instagram.com/static/images/homepage/phones/home-phones.png/1dc085cdb87d.png);
}

.--main-bg {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  visibility: hidden;
}

.--bg-previous {
  visibility: visible;
  opacity: 0;
  transition: opacity 2s ease-out;
}

.--bg-current {
  visibility: visible;
  opacity: 1;
  z-index: 100;
  transition: opacity 2s ease-out;
}

